.Mypage__Container {
  width: 100%;
  height: 100vh;
  .Mypage__Grid {
    display: grid;
    height: 90vh;
    grid-template-columns: 1fr;
    grid-template-rows: 0.3fr 0.5fr repeat(2, 1fr);
    font-size: 25px;
    font-weight: 500;
    > section {
      &:first-child {
        margin-top: -10px;
      }
      &:last-child {
        margin-top: -10px;
      }
      margin-top: 10px;
      padding-left: 20px;
    }
    .Mypage__Header {
      font-size: 30px;
      display: flex;
      padding-left: 20px;
      > button {
        color: tomato;
        background: none;
        cursor: pointer;
        border: none;
        line-height: 2;
        margin: 8px 0 0 30px;
        font-weight: 700;
      }
      .Mypage__Empty {
        color: teal;
        position: absolute;
        right: 40%;
        top: 45%;
        margin: 35px 0 0 50px;
        font-size: 20px;
        font-weight: 400;
      }
      .Mypage__Title {
        display: flex;
        align-items: center;
      }
    }
    .Mypage__Taste {
      > button {
        margin-right: 10px;
        transition: 0.5s;
        font-size: 16px;
        font-weight: 700;
        &:hover {
          background-color: tomato;
          transition: 0.5s;
        }
      }
    }
  }
}
